body{
    background:radial-gradient(#e5e5e5,#ffff,#e5e5e5);
}

.card{
    width: 20rem;
    border-radius: 12px !important;
    height: 488px;
}

.card:hover{
    box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253) !important;
}

.card-body{
    padding: 1.5rem 0 !important;
}

.card-text{
    font-size: .9rem;
    padding: 0.4rem 1.9rem;
    height: 163px;
}

.Home {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
}
.titleBox {
  margin-top: 3%;
}
.title {
  font-weight: 800;
  color: black;
  text-align: center;
}
.subtitle {
  text-align: center;
}
.projectsContainer {
  background: linear-gradient(180deg, #fff 50%, #7510f7 50%);
}
.section.is-primary {
  background-color: #7510f7;
  color: #fff;
}
h3 {
  color: #fff !important;
  font-weight: 400 !important;
}
.cardMiniHeader {
  color: #7510f7 !important;
  margin-top: 50px;
}
.skills {
  background: linear-gradient(180deg, #7510f7 50%, #ffffff 50%);
}



.col-sm-auto{
    padding-top: 20px;
    margin: auto;
}


.box {
  margin: auto;
  width: 80%;
  border-radius: 12px;
  box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1),
    0 0 0 1px rgba(10, 10, 10, 0.02);
}
.columns {
  display: flex;
  flex-direction: row;
  margin-bottom: 30px !important;
}
.column {
  padding-left: 50px;
  padding-right: 50px;
}
.left {
  border-right: 1px solid #e6ecf8;
}

.skillogo {
  height: 55px;
  width: 55px;
  margin: 25px;
}

@media screen and (max-width: 1200px) {
  .box {
    width: 90%;
  }
}

@media screen and (max-width: 880px) {
  .columns {
    flex-direction: column;
  }
  .column {
    padding-left: 40px;
    padding-right: 40px;
  }
  .left {
    border-bottom: 1px solid #e6ecf8;
    border-right: none;
  }
  .skill {
    margin-left: 15%;
    margin-right: 15%;
  }
}

.Footer{
    padding-top: 220px;
    background-color: #fff !important;
}

.links{
  display: flex;
  flex-direction:column;
  justify-content: end;
  background-color: #7510F7;
  height: 300px;
  width: 100%; 
}

.linksContainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.linkBox{
  margin: 14px;
}


.linkedInLogo{
  border: 2.5px solid #934ad8;
  height: 50px;
  border-radius: 50%;
}
.linkedInLogo:hover{
  border: none;
  height: 50px;
  border-radius: 50%;
}

.gitHubLogo{
  border: 2.5px solid #934ad8;
  height: 50px;
  border-radius: 50%;
}
.gitHubLogo:hover{
  border: none;
  height: 50px;
  border-radius: 50%;
}
